section.type {
  background-color: #708090a8;
  margin-top: 25px;
  padding-block: 35px;
}

.type-card-container {
  margin-top: 20px;
}

.type-card-container ul {
  justify-content: space-around;
  gap: 10px;
  line-height: 0;
  display: flex;
}

.type-card {
  text-align: center;
  color: beige;
  flex-direction: column;
  justify-content: space-between;
  width: 130px;
  min-height: 165px;
  padding: 10px;
  display: flex;
}

.img-holder {
  cursor: pointer;
  background-color: #f1a61e;
  border: 2px solid beige;
  border-radius: 50%;
  width: 110px;
  display: block;
}

.type-card input:checked + .img-holder {
  border-start-end-radius: 10px;
}

.img-holder img {
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

.type-card p {
  line-height: initial;
  margin-top: 10px;
}

input#custom-type {
  text-align: center;
  border-radius: 20px;
  width: 110px;
  margin: 10px 0 0;
  padding-inline: 10px;
}

.type-card input#other:checked ~ p {
  display: none;
}

.type-card input#other:checked ~ input#custom-type {
  display: block;
}

.location {
  height: 600px;
  margin-top: 25px;
}

.location .content-stripe {
  height: 100%;
  position: relative;
}

.location-descriptor {
  clip-path: polygon(0 0, 80% 0, 100% 50%, 80% 100%, 0 100%);
  z-index: 5;
  padding: 20px;
  background-color: #f1a61e;
  width: 330px;
  padding-right: 90px;
  position: absolute;
  top: 0;
}

.location-descriptor h2 {
  text-align: center;
  margin-bottom: 20px;
}

.location-descriptor p {
  padding-left: 25px;
}

.location-input {
  margin-top: 20px;
  display: flex;
}

.location-input input {
  border: 1px solid gray;
  border-radius: 10px 0 0 10px;
  width: 90%;
  margin: 0;
  padding-block: 5px;
}

.location-input button {
  border: 1px solid gray;
  border-radius: 9px;
  width: 15%;
  margin-left: -7px;
}

#map {
  clip-path: polygon(0 0, 100% 0, 100% 90%, 15% 100%);
  cursor: pointer;
  background-color: azure;
  width: 100%;
  height: 100%;
}

.milestones {
  margin-top: -50px;
}

.milestones img {
  border-radius: 50%;
  width: 80px;
  height: 80px;
}

.milestones .content-stripe {
  clip-path: polygon(0 0, 85% 0, 75% 85%, 0 100%);
  padding: 40px;
  background-color: #a1abb6;
  padding-bottom: 80px;
}

.milestone-bubbles {
  width: 600px;
  margin: 20px 0 0 40px;
  display: inline-block;
}

.milestone-bubbles ul {
  text-align: center;
  flex-wrap: wrap;
  gap: 40px;
  display: flex;
}

.milestone-bubbles li {
  cursor: pointer;
  width: min-content;
  position: relative;
}

.milestone-bubbles img {
  object-fit: cover;
  border: 2px solid #fff;
}

.milestone-bubbles p {
  word-break: break-word;
  overflow-wrap: break-word;
}

.milestone-bubbles li:hover:before {
  content: "âœ—";
  color: #919191ed;
  background-color: #e1e1e1b8;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  font-size: 50px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 0;
}

.milestone-bubbles li:after {
  content: "";
  z-index: -5;
  border: 2px solid #fff;
  width: 80px;
  position: absolute;
  top: 40px;
}

.milestone-bubbles li:last-child:after {
  width: 0;
}

.milestone-inputs {
  margin-block: 30px;
  align-items: center;
  gap: 30px;
  margin-left: 40px;
  display: flex;
}

.milestone-inputs > label {
  width: 80px;
  display: block;
}

.milestone-inputs img {
  object-fit: cover;
  cursor: pointer;
  border: 2px solid #00aa14;
}

.milestone-name-input {
  width: 40%;
  display: flex;
}

.milestone-name-input input {
  border: 1px solid gray;
  width: 85%;
  margin: 0;
  padding-block: 5px;
}

.milestone-name-input button {
  border: 1px solid gray;
  width: 15%;
}

.milestones-info {
  font-size: .9em;
  font-style: italic;
}

.resources {
  margin-bottom: 50px;
}

.resources h2 {
  text-align: right;
  margin-right: 25px;
}

.resources-icons {
  justify-content: center;
  display: flex;
}

.resources-icons li {
  text-align: center;
  border-top: 3px solid #0000;
  width: 100px;
  display: inline-block;
}

.resources-icons li.selected {
  border-color: #00f;
}

.resources-icons .icon-holder {
  opacity: .2;
  cursor: pointer;
  margin-top: 10px;
  display: block;
}

.resources-icons input:checked + .icon-holder {
  opacity: 1;
}

.resources-icons li label {
  cursor: pointer;
  border: 5px solid #26c4c1;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin-top: 10px;
  display: inline-block;
}

.resources-icons input:checked ~ label {
  background-color: #0a669c;
}

.resources-icons li img {
  width: 50px;
  height: 50px;
}

.resources-icons {
  margin-block: 50px;
}

.resources-details {
  padding-inline: 70px;
  display: flex;
}

.resources-details .settings {
  flex-shrink: 0;
  width: 300px;
