  margin-bottom: 15px;
  display: flex;
}

.priority .selection-bar {
  display: flex;
}

.priority label {
  border: 1px solid #000;
  width: 25px;
  height: 15px;
  display: inline-block;
}

.priority label:not(:first-of-type) {
  border-left: none;
}

.priority label:has(input:checked) {
  background-color: #0e4695;
}

.limit input {
  border: 1px solid #cbcbcb;
  width: 80px;
  margin: 0;
  display: inline;
}

.authenticate input {
  width: 20px;
  height: 20px;
  margin: 0;
}

.resources-details .notes {
  width: 100%;
}

.notes textarea {
  width: 100%;
  height: 100%;
}

.control-btn {
  margin-left: 50px;
  padding: 10px 20px;
}

.resources-user-info {
  height: 200px;
  margin-inline: 70px;
  line-height: 2;
}

.resources-user-info p {
  border-left: 5px solid #26c4c1;
  padding: 10px 20px;
}

.deadline {
  z-index: 5;
  position: relative;
}

.deadline-bg {
  clip-path: polygon(0 50px, 100% 0, 100% 100%, 30px calc(100% - 30px));
  z-index: -5;
  background-color: #cbcbcb;
  width: calc(50% - 112px);
  height: 100%;
  position: absolute;
  right: 0;
}

.deadline .content-stripe {
  flex-direction: column;
  align-items: flex-end;
  padding: 60px 25px;
  display: flex;
}

.deadline .header span {
  text-align: left;
}

.deadline input {
  border: 1px solid #cbcbcb;
