  margin-top: -150px;
  position: relative;
}

.media .content-stripe {
  padding: 80px 25px 25px;
}

.media-bg {
  clip-path: polygon(0 0, 75% 70px, 100% 100%, 0 100%);
  z-index: -10;
  background-color: #ededed;
  width: calc(50% + 612px);
  height: 100%;
